<template>
  <div>
    <p>使用ref访问子组件,并调用子组件的方法,改变子组件的属性</p>
    <Coma ref="coinput" />
    <div>
      <button @click="handle">点击我, 改变子组件的值,调用子组件方法来聚焦</button>
    </div>
  </div>
</template>

<script>
import Coma from "@/components/Coma.vue";
export default {
  components: {
    Coma,
  },
  methods: {
    handle() {
      // 可以访问到子组件中的 数据 方法, 等
      this.$refs.coinput.msg = "i love you";
      this.$refs.coinput.focus();
    },
  },
};
</script>

