<template>
  <div style="border:1px solid; margin-top:20px;padding:15px;">
    我是子组件 ,
    <br/>
    <button @click="handle">子组件按钮</button>
    <br>
    {{ msg }}
    <br>
  </div>
</template>

<script>
export default {
  // 后代组件中, 使用 inject 来接受祖先组件提供的数据
  inject: ["getData"],
  data() {
    return {
      msg: "我是子组件数据",
    };
  },
  methods: {
    handle() {
      this.msg = this.getData();
    },
  },
};
</script>

