<template>
  <div>
    <el-popover width="400" v-model="isPopoverShow" trigger="click" placement="bottom-start" :offset="120" :visible-arrow="false" :disabled="!curSearchConfig.popover">
      <!-- 弹框内容部分 -->
      <!-- 可多选的状态集合 -->
      <div v-if="curSearchConfig.popover" class="status-box">
        <div class="select-drop-checkboxWarp">
          <el-checkbox-group v-model="checkoukGroupValue" @change="setCurSearchConfigValue">
            <el-checkbox v-for="item in curSearchConfig.popover.config" :key="item.key" :label="item.key" class="select-drop-checkbox">
              {{ item.label }}
            </el-checkbox>
          </el-checkbox-group>
        </div>
        <el-row class="select-drop-btn">
          <el-button type="primary" size="mini" @click="onPopoverSearch">确定</el-button>
          <el-button size="mini" @click="isPopoverShow = false">取消</el-button>
        </el-row>
      </div>
      <!-- 触发弹框部分 -->
      <el-input placeholder="请输入内容" v-model="curSearchParams.value" slot="reference" size="small" style="width: 420px;">
        <template slot="prepend">
          <el-select v-model="curSearchParams.key" placeholder="请选择" style="width: 120px;" @change="changeSearchType">
            <el-option v-for="item in searchConfig" :key="item.key" :value="item.key" :label="item.label" />
          </el-select>
        </template>
        <el-button slot="append" icon="el-icon-search" @click="onSearch"></el-button>
      </el-input>
    </el-popover>
    <div v-show="Object.keys(searchParams).length > 0">
      <span>检索项:</span>
      <el-tag v-for="(value,key) in searchParams" :key="key" closable 
      @close='deleteSearchParams(key)'>
        {{keyTolabel(key)}}:{{getValue(key,value)}}
      </el-tag>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isPopoverShow: false,
      input3: "",
      searchConfig: [
        {
          label: "组件名称",
          key: "name",
          placeholder: "请输入组件名称进行模糊查询",
        },
        {
          label: "组件ID",
          key: "uuid",
          placeholder: "请输入组件ID进行精准查询",
        },
        {
          label: "源IP",
          key: "src",
          placeholder: "请输入源IP进行精确查询",
          popover: {
            config: [
              {
                label: "高危",
                key: 1,
                code: 1,
              },
              {
                label: "中危",
                key: 2,
                code: 2,
              },
              {
                label: "低危",
                key: 3,
                code: 3,
              },
            ],
          },
        },
      ],
      curSearchConfig: [],
      //   当前的搜索参数
      curSearchParams: {
        key: "",
        value: "",
      },
      searchParams: {},
      value: "",
      checkoukGroupValue: [],
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    //   其实主要就是拿到值, 转化后展示 和转化后传参
    init() {
      //   初始化时, 将第一个搜索配置当作 当前的搜索参数
      this.curSearchConfig = this.searchConfig[0];

      this.curSearchParams.key = this.searchConfig[0].key;
    },
    deleteKeyInSearchParams(key) {
      const searchParams = { ...this.searchParams }
      delete searchParams[key]
      return searchParams
    },
      // 删除检索项
    deleteSearchParams(key) {
      const searchParams = key ? this.deleteKeyInSearchParams(key) : {}
      this.searchParams = searchParams
      console.log('00', this.searchParams)
    //   this.$emit('handleSearchParams', searchParams)
      // 清空popover的值
      this.checkoukGroupValue = []
    },
    // 根据key 得到 label
    keyTolabel(key) {
      const temp = this.searchConfig.find((item) => {
        return item.key === key;
      });
      return temp.label;
    },
    // 值转化
    getValue(key, value) {
      const temp = this.searchConfig.find((item) => {
        return item.key === key;
      });
      if (temp.popover) {
        const labelList = value.map((v) => {
          const sameKeyItem = temp.popover.config.find((i) => {
            return i.key === v;
          });
          return sameKeyItem.label;
        });
        return labelList.join(" | ");
      } else {
        return value;
      }
    },
    //   搜索的时候拿到当前的搜索参数, 然后将当前的搜索参数和以前的合并在一起, 最后通知外面触发搜索事件
    onSearch() {
      console.log("curSearchParams", this.curSearchParams);
      const { key, value } = this.curSearchParams;
      let keylist = [];
      if (value) {
        if (this.curSearchConfig.popover) {
          const temp = value.split(" | "); // ["高危", "中危"]
          console.log("temp", temp);
          keylist = temp.map((v) => {
            const sameLabelItem = this.curSearchConfig.popover.config.find(
              (i) => {
                return (i.label = v);
              }
            );
            return sameLabelItem.key;
          });
        }
        this.searchParams = {
          ...this.searchParams,
          //   对象的属性名, 如果是一个变量,用[]
          [key]: this.curSearchConfig.popover ? keylist : value,
        };
      } else {
        //   当当前搜索值为空时, 不更新搜索数据
        this.curSearchParams.value = "";
      }

      console.log("searchParams", this.searchParams);
      //   搜索时 , 接口字段是 this.value  ,字段对应的值是 this.input3
    },
    // popover 搜索回调
    onPopoverSearch() {
      this.isPopoverShow = false;
      this.onSearch();
    },
    // 隐藏popover
    hidePopover() {
      this.isPopoverShow = false;
      //   this.$refs.popoverSearchInput.doClose()
    },
    // 清空当前搜索的参数
    clearCurSearchParams() {
      this.checkoukGroupValue = [];
      this.curSearchParams.value = "";
    },
    // 设置当前搜索类型下的配置
    setCurSearchConfig(searchKey) {
      this.curSearchConfig = this.searchConfig.find(
        (item) => item.key === searchKey
      );
    },
    // 更改搜索类型 , 关闭popover弹框 , 清空当前搜索参数 ,设置当前搜索配置
    changeSearchType(searchKey) {
      // 搜索的key  searchkey
      console.log("searchkey", searchKey);
      this.hidePopover();
      this.clearCurSearchParams();
      this.setCurSearchConfig(searchKey);
      // 如果存在popover类型，反显勾选值
      //   this.curSearchConfig.popover && this.inversionRecovery(searchKey)
    },
    // 设置搜索框value , 状态值多选后,把状态值转化为 中文来展示 ,
    // 之后提交的时候, 会将这个状态值转化回来进行传参
    setCurSearchConfigValue(value) {
      // value =  [1,2]  checkoukGroupValue = [1,2]
      console.log("多选集合", value);
      console.log(this.checkoukGroupValue);
      const labelList = this.checkoukGroupValue.map((v) => {
        const sameKeyItem = this.curSearchConfig.popover.config.find((i) => {
          return i.key === v;
        });
        return sameKeyItem.label;
      });
      this.curSearchParams.value = labelList.join(" | ");
      //   this.curSearchParams.value = this.formatSearchValue(value);
    },
  },
};
</script>

