<template>
  <div>
    <button @click="handle">父组件按钮</button>
    <br>
    <div>
      {{fmsg}}
    </div>
    <div>
      <Comthree />
    </div>
  </div>
</template>

<script>
import Comthree from "@/components/Comthree.vue";
export default {
  components: {
    Comthree,
  },
  data() {
    return {
      current: "Comone",
      fmsg: "我是父组件数据",
    };
  },
  // 祖先组件 用 provide 来给后代组件提供数据
  provide: {
    getData() {
      return "父组件提供的方法";
    },
  },
  methods: {
    handle(){
      console.log( this.fmsg)
    }
  },
};
</script>

