<template>
  <div>
    <div class="box-wrapper">
      <slot name="header">头部插槽默认内容</slot>
    </div>
    <hr>
    <div class="box-wrapper">
      <slot>我是插槽默认内容</slot>
    </div>
    <div class="box-wrapper">
      <slot name="footer">底部插槽默认内容</slot>
    </div>
    <hr>
    <div class="box-wrapper">
      <!-- 作用域插槽, 绑定了外面可能需要使用的值 user , 使得父作用域有办法可以访问到子作用域的值 -->
      <!-- 当使用组件时, 没有提供 具名插槽one 的内容的时候, 这里会显示 user.like 的值 . 否则显示提供的值 -->
      <slot name="one"
        v-bind:user='user'
        :foot='foot'
        >
        {{user.like}}
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        name: "in - hh",
        num: "in - 00",
        like: "in - apple",
      },
      foot:{
        red: 'apple',
        yellow: 'belala'
      }
    };
  },
};
</script>

