<template>
  <div>
    <p>使用 aync修饰符, 子组件更新父组件的值</p>
    <hr>
       这是从父组件传过来的title: 
       <br>
       {{newtitle}}
      <div class="box-wrapper">
          <el-input v-model="input" clearable></el-input>
          <button @click="handle">改变title</button>
      </div>
  </div>
</template>

<script>
export default {
    props:{
        title:{
            typeof: String,
            default:'标题'
        }
    },
  data() {
    return {
      newtitle : this.title,
      input:'',
    };
  },
  methods: {
      handle(){
          this.newtitle = this.input? this.input : 'uioo'
        //   如果不做下面这一步, 在子组件中改变title,不会同步更改父组件中的title,因为数据是单向的
        // 如果使用下面的方式处理, 数据会同步到父组件 ,也就是双向数据绑定
          this.$emit('update:title',this.newtitle)
      },
    //   递归
    test(){
        
    }

  },
};
</script>

